
















import { Vue, Component, Prop } from "vue-property-decorator";
@Component
export default class WeaponSelector extends Vue {

    mItems = []

    mItems2 = []
    
    mSelectedItem = null;

    mounted(){
      this.$http.get("/weapon.json").then(res=>{
        this.mItems = res.data
      })
    }

    selectedItem(){
        return this.mSelectedItem;
    }

}
